import React from 'react';
import Button from '@material-ui/core/Button';
import CardContent from '@material-ui/core/CardContent';
import TextField from '@material-ui/core/TextField';
import ArrowForwardIcon from '@material-ui/icons/ArrowForwardSharp';
import ArrowBackIcon from '@material-ui/icons/ArrowBackSharp';
import { Link } from '@reach/router';

import { Layout, Wrapper, Arrow, CardHeader } from '../components';
import Drama from '../models/Drama';
import Person from '../models/Person';
import OuterChain from '../models/OuterChain';
import dramaDefault from '../utils/drama';
import Helmet from 'react-helmet';

const HTMLTitle = '填写详细信息';

interface GenerateState {
  drama: Drama;
  title: string;
}

export default class Generate extends React.Component<{}, GenerateState> {
  constructor(props: any) {
    super(props);
    let drama: Drama;
    if (typeof window !== `undefined`) {
      const dramaJson: any = window.localStorage.getItem('drama');
      drama = JSON.parse(dramaJson) || dramaDefault;
    } else {
      drama = dramaDefault;
    }
    this.state = { drama, title: HTMLTitle };

    this.handleInputChange = this.handleInputChange.bind(this);
    this.handleOnSave = this.handleOnSave.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  // 获得target的name和value的值
  // 根据name和value生成需要对应的更新键值
  // 负责的是更新drama的键值
  handleInputChange(event: any) {
    const target = event.target;
    const name: string = target.name;
    const value: any = target.value;
    const drama: Drama = Object.assign({}, this.state.drama, { [name]: value });

    this.setState({ drama });
  }

  // 保存数据
  // 1. 根据表单数据，生成最新的drama数据
  // 2. 根据最新drama再生成表单数据（保证数据一致）
  // 3. 将drama存储在localStorage里
  // 4. 更新state
  handleOnSave() {
    const staff: Person[] = this.peopleToObj(this.state.drama.staffStr);
    const cast: Person[] = this.peopleToObj(this.state.drama.castStr);
    const ed: Person[] = this.peopleToObj(this.state.drama.edStr);
    const links: OuterChain[] = this.linksToObj(this.state.drama.linkStr);
    const staffStr: string = this.peopleToStr(staff);
    const castStr: string = this.peopleToStr(cast);
    const edStr: string = this.peopleToStr(ed);
    const linkStr: string = this.linksToStr(links);
    const drama: Drama = Object.assign({}, this.state.drama, { staff, cast, ed, staffStr, castStr, edStr, links, linkStr });

    if (typeof window !== `undefined`) {
      window.localStorage.setItem('drama', JSON.stringify(drama));
    }
    this.setState(() => {
      return { drama };
    });
  }

  // 生成代码
  // 1. 保存数据
  handleSubmit() {
    this.handleOnSave();
  }

  // 将people对象转为string
  // 遍历people,将每个person传给personToStr, 得到每个person的str
  peopleToStr(people: Person[]) {
    let str: string = '';
    people.forEach((person: Person) => {
      str += this.personToStr(person);
    });
    return str;
  }

  // 将person对象，转为对应的str.
  // e.g. 策划：策划的名字【策划的社团】@策划的微博
  personToStr(person: Person) {
    const organization = person.organization ? `【${person.organization}】` : '';
    const weibo = person.weibo ? `@${person.weibo}` : '';
    return `${person.job}：${person.name}${organization}${weibo}\n`;
  }

  // 将people字符串，转换为对应的obj.
  // 根据\n 来区分不同的person
  // list 最后一个为空
  peopleToObj(people: string) {
    const arr: Person[] = [];
    const list = people.split('\n');

    list.forEach((person: string) => {
      if (!person) {
        return;
      }
      arr.push(this.personToObj(person));
    });
    return arr;
  }

  // 根据正则得到person的各个键的值
  personToObj(person: string) {
    const job: RegExp = /([\u4E00-\u9FA5A-Za-z0-9]+)：/;
    const name: RegExp = /：([\u4E00-\u9FA5A-Za-z0-9《》]+)/;
    const org: RegExp = /【([\u4E00-\u9FA5A-Za-z0-9]+)】/;
    const weibo: RegExp = /@([\u4E00-\u9FA5A-Za-z0-9]+)/;

    return {
      job: this.filterReObj(job.exec(person)),
      name: this.filterReObj(name.exec(person)),
      organization: this.filterReObj(org.exec(person)),
      weibo: this.filterReObj(weibo.exec(person)),
    };
  }

  linksToStr(links: OuterChain[]) {
    let str: string = '';
    links.forEach(link => {
      str += this.linkToStr(link);
    });
    return str;
  }

  linkToStr(link: OuterChain) {
    return `${link.name}：${link.url}【${link.other}】\n`;
  }

  linksToObj(links: string) {
    const arr: OuterChain[] = [];
    const list = links.split('\n');

    list.forEach(link => {
      if (!link) {
        return;
      }
      arr.push(this.linkToObj(link));
    });
    return arr;
  }

  linkToObj(link: string) {
    const name: RegExp = /([\u4E00-\u9FA5A-Za-z0-9]+)：/;
    const url: RegExp = /((?:http|https):[\w\/\-.?%&=]+)/;
    const other: RegExp = /【([\w]+)】/;

    return {
      name: this.filterReObj(name.exec(link)),
      url: this.filterReObj(url.exec(link)),
      other: this.filterReObj(other.exec(link)),
    };
  }

  // 将re对象中，需要的数据取出来
  filterReObj(reObj: any) {
    if (!reObj) return '';
    return reObj[1];
  }

  public render() {
    const { drama, title } = this.state;

    return (
      <Layout>
        <Helmet title={title} />
        <Wrapper>
          <CardHeader>
            <h2>{title}</h2>
          </CardHeader>
          <CardContent className={`card-title`} />
          <CardContent>
            <TextField
              fullWidth={true}
              variant="filled"
              label="广播剧名"
              name="title"
              required={true}
              value={drama.title}
              onChange={this.handleInputChange}
            />
            <TextField
              fullWidth={true}
              variant="filled"
              label="副标题"
              name="subtitle"
              required={true}
              value={drama.subtitle}
              onChange={this.handleInputChange}
            />
            <TextField
              fullWidth={true}
              variant="filled"
              label="第N期"
              name="episode"
              required={true}
              value={drama.episode}
              onChange={this.handleInputChange}
            />
            <TextField
              fullWidth={true}
              variant="filled"
              label="正剧海报"
              name="dramaPoster"
              required={true}
              prefix="https://"
              InputProps={{}}
              value={drama.dramaPoster}
              onChange={this.handleInputChange}
            />
            <TextField
              fullWidth={true}
              variant="filled"
              label="制作组名单"
              name="staffStr"
              value={drama.staffStr}
              multiline
              rows={4}
              rowsMax={9}
              required={true}
              onChange={this.handleInputChange}
            />
            <TextField
              fullWidth={true}
              variant="filled"
              label="配音组名单"
              name="castStr"
              value={drama.castStr}
              multiline
              rows={4}
              rowsMax={9}
              required={true}
              onChange={this.handleInputChange}
            />
            <TextField
              fullWidth={true}
              variant="filled"
              label="片尾曲名单"
              name="edStr"
              value={drama.edStr}
              multiline
              rows={4}
              rowsMax={9}
              required={true}
              onChange={this.handleInputChange}
            />
            <TextField
              fullWidth={true}
              variant="filled"
              label="链接列表"
              name="linkStr"
              value={drama.linkStr}
              multiline
              rows={4}
              rowsMax={5}
              required={true}
              onChange={this.handleInputChange}
            />
          </CardContent>
          <Arrow>
            <Link to="/select" onClick={this.handleSubmit}>
              <Button size="large" className={`button`} variant="contained" color="primary">
                生成
                <ArrowForwardIcon />
              </Button>
            </Link>
            <Link to="/" onClick={this.handleOnSave}>
              <Button size="large" className={`button`} variant="contained" color="secondary">
                <ArrowBackIcon />
                上一步
              </Button>
            </Link>
          </Arrow>
        </Wrapper>
      </Layout>
    );
  }
}
